import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Grid,
  Paper,
  TextField,
  Button,
  Typography,
  Box,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Stepper,
  Step,
  StepLabel,
  Slider,
} from '@mui/material';

function DetailedFinancialForm({ onSubmit, initialData }) {
  const navigate = useNavigate();
  const [activeStep, setActiveStep] = useState(0);
  const steps = ['', '', '', '', ''];

  const defaultFormData = {
    // 
    name: '',
    age: '',
    occupation: '',
    retirementAge: '',
    
    // 
    monthlyIncome: '',
    yearEndBonus: '',
    investmentIncome: '',
    otherIncome: '',
    
    // 
    expectedSalaryGrowth: 3,
    expectedBonusGrowth: 3,
    expectedInvestmentReturn: 5,
    
    // 
    rentMortgage: '',
    dailyExpenses: '',
    transportation: '',
    insurance: '',
    education: '',
    entertainment: '',
    otherExpenses: '',
    
    // 
    expectedLifestyleChanges: [
      { year: '', description: '', amount: '' }
    ],
    plannedMajorExpenses: [
      { year: '', description: '', amount: '' }
    ],
    expectedExpenseGrowth: 2,
    
    // 
    savings: '',
    stocks: '',
    bonds: '',
    realEstate: '',
    otherAssets: '',
    
    // 
    stocksAllocation: '',
    bondsAllocation: '',
    realEstateAllocation: '',
    cashAllocation: '',
    
    // 
    expectedStockReturn: 8,
    expectedBondReturn: 4,
    expectedRealEstateReturn: 4,
    expectedCashReturn: 1,
    
    // 
    totalDebt: '',
    monthlyDebtPayment: '',
    debtInterestRate: '',
    
    // 
    financialGoals: '',
    riskTolerance: 'medium',
  };

  const [formData, setFormData] = useState(defaultFormData);

  useEffect(() => {
    if (initialData) {
      setFormData(initialData);
    }
  }, [initialData]);

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData(prevData => ({
      ...prevData,
      [name]: value
    }));
  };

  const handleSliderChange = (name) => (event, newValue) => {
    setFormData(prevData => ({
      ...prevData,
      [name]: newValue
    }));
  };

  const handleNext = () => {
    setActiveStep((prevStep) => prevStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevStep) => prevStep - 1);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    onSubmit(formData);
    navigate('/');
  };

  const renderStepContent = (step) => {
    switch (step) {
      case 0:
        return (
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Typography variant="h6" gutterBottom>
                
              </Typography>
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                required
                fullWidth
                label=""
                name="name"
                value={formData.name}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                required
                fullWidth
                label=""
                name="age"
                type="number"
                value={formData.age}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                required
                fullWidth
                label=""
                name="occupation"
                value={formData.occupation}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                required
                fullWidth
                label=""
                name="retirementAge"
                type="number"
                value={formData.retirementAge}
                onChange={handleChange}
              />
            </Grid>
          </Grid>
        );

      case 1:
        return (
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Typography variant="h6" gutterBottom>
                
              </Typography>
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                required
                fullWidth
                label=" ()"
                name="monthlyIncome"
                type="number"
                value={formData.monthlyIncome}
                onChange={handleChange}
                InputProps={{
                  endAdornment: <Typography color="textSecondary"></Typography>
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label=" ()"
                name="yearEndBonus"
                type="number"
                value={formData.yearEndBonus}
                onChange={handleChange}
                InputProps={{
                  endAdornment: <Typography color="textSecondary"></Typography>
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label=" (/)"
                name="investmentIncome"
                type="number"
                value={formData.investmentIncome}
                onChange={handleChange}
                InputProps={{
                  endAdornment: <Typography color="textSecondary"></Typography>
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label=" (/)"
                name="otherIncome"
                type="number"
                value={formData.otherIncome}
                onChange={handleChange}
                InputProps={{
                  endAdornment: <Typography color="textSecondary"></Typography>
                }}
              />
            </Grid>
            <Grid item xs={12}>
              <Typography gutterBottom>
                 (%)
              </Typography>
              <Slider
                value={formData.expectedSalaryGrowth}
                onChange={handleSliderChange('expectedSalaryGrowth')}
                aria-labelledby="salary-growth-slider"
                valueLabelDisplay="auto"
                step={0.5}
                marks
                min={0}
                max={10}
              />
            </Grid>
            <Grid item xs={12}>
              <Typography gutterBottom>
                 (%)
              </Typography>
              <Slider
                value={formData.expectedInvestmentReturn}
                onChange={handleSliderChange('expectedInvestmentReturn')}
                aria-labelledby="investment-return-slider"
                valueLabelDisplay="auto"
                step={0.5}
                marks
                min={0}
                max={15}
              />
            </Grid>
          </Grid>
        );

      case 2:
        return (
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Typography variant="h6" gutterBottom>
                
              </Typography>
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                required
                fullWidth
                label="/ (/)"
                name="rentMortgage"
                type="number"
                value={formData.rentMortgage}
                onChange={handleChange}
                InputProps={{
                  endAdornment: <Typography color="textSecondary"></Typography>
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                required
                fullWidth
                label=" (/)"
                name="dailyExpenses"
                type="number"
                value={formData.dailyExpenses}
                onChange={handleChange}
                InputProps={{
                  endAdornment: <Typography color="textSecondary"></Typography>
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                required
                fullWidth
                label=" (/)"
                name="transportation"
                type="number"
                value={formData.transportation}
                onChange={handleChange}
                InputProps={{
                  endAdornment: <Typography color="textSecondary"></Typography>
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                required
                fullWidth
                label=" (/)"
                name="insurance"
                type="number"
                value={formData.insurance}
                onChange={handleChange}
                InputProps={{
                  endAdornment: <Typography color="textSecondary"></Typography>
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label=" (/)"
                name="education"
                type="number"
                value={formData.education}
                onChange={handleChange}
                InputProps={{
                  endAdornment: <Typography color="textSecondary"></Typography>
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label=" (/)"
                name="entertainment"
                type="number"
                value={formData.entertainment}
                onChange={handleChange}
                InputProps={{
                  endAdornment: <Typography color="textSecondary"></Typography>
                }}
              />
            </Grid>
            <Grid item xs={12}>
              <Typography gutterBottom>
                 (%)
              </Typography>
              <Slider
                value={formData.expectedExpenseGrowth}
                onChange={handleSliderChange('expectedExpenseGrowth')}
                aria-labelledby="expense-growth-slider"
                valueLabelDisplay="auto"
                step={0.5}
                marks
                min={0}
                max={10}
              />
            </Grid>
          </Grid>
        );

      case 3:
        return (
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Typography variant="h6" gutterBottom>
                
              </Typography>
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                required
                fullWidth
                label=" ()"
                name="savings"
                type="number"
                value={formData.savings}
                onChange={handleChange}
                InputProps={{
                  endAdornment: <Typography color="textSecondary"></Typography>
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                required
                fullWidth
                label=" ()"
                name="stocks"
                type="number"
                value={formData.stocks}
                onChange={handleChange}
                InputProps={{
                  endAdornment: <Typography color="textSecondary"></Typography>
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                required
                fullWidth
                label=" ()"
                name="bonds"
                type="number"
                value={formData.bonds}
                onChange={handleChange}
                InputProps={{
                  endAdornment: <Typography color="textSecondary"></Typography>
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                required
                fullWidth
                label=" ()"
                name="realEstate"
                type="number"
                value={formData.realEstate}
                onChange={handleChange}
                InputProps={{
                  endAdornment: <Typography color="textSecondary"></Typography>
                }}
              />
            </Grid>
            <Grid item xs={12}>
              <Typography variant="subtitle1" gutterBottom>
                
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Typography gutterBottom>
                 (%)
              </Typography>
              <Slider
                value={formData.expectedStockReturn}
                onChange={handleSliderChange('expectedStockReturn')}
                aria-labelledby="stock-return-slider"
                valueLabelDisplay="auto"
                step={0.5}
                marks
                min={0}
                max={15}
              />
            </Grid>
            <Grid item xs={12}>
              <Typography gutterBottom>
                 (%)
              </Typography>
              <Slider
                value={formData.expectedBondReturn}
                onChange={handleSliderChange('expectedBondReturn')}
                aria-labelledby="bond-return-slider"
                valueLabelDisplay="auto"
                step={0.5}
                marks
                min={0}
                max={10}
              />
            </Grid>
            <Grid item xs={12}>
              <Typography gutterBottom>
                 (%)
              </Typography>
              <Slider
                value={formData.expectedRealEstateReturn}
                onChange={handleSliderChange('expectedRealEstateReturn')}
                aria-labelledby="real-estate-return-slider"
                valueLabelDisplay="auto"
                step={0.5}
                marks
                min={0}
                max={10}
              />
            </Grid>
          </Grid>
        );

      case 4:
        return (
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Typography variant="h6" gutterBottom>
                
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                multiline
                rows={4}
                label=""
                name="plannedMajorExpenses"
                placeholder="
2025 200
2026 100
2030 150"
                value={formData.plannedMajorExpenses}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                multiline
                rows={4}
                label=""
                name="expectedLifestyleChanges"
                placeholder="
2026 2
2028 3"
                value={formData.expectedLifestyleChanges}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <FormControl fullWidth>
                <InputLabel></InputLabel>
                <Select
                  name="riskTolerance"
                  value={formData.riskTolerance}
                  onChange={handleChange}
                  label=""
                >
                  <MenuItem value="low"></MenuItem>
                  <MenuItem value="medium"></MenuItem>
                  <MenuItem value="high"></MenuItem>
                </Select>
              </FormControl>
            </Grid>
          </Grid>
        );

      default:
        return null;
    }
  };

  return (
    <Container maxWidth="md" sx={{ mt: 4, mb: 4 }}>
      <Paper sx={{ p: 3 }}>
        <Typography variant="h5" gutterBottom>
          
        </Typography>
        
        <Stepper activeStep={activeStep} sx={{ pt: 3, pb: 5 }}>
          {steps.map((label) => (
            <Step key={label}>
              <StepLabel>{label}</StepLabel>
            </Step>
          ))}
        </Stepper>

        <form onSubmit={handleSubmit}>
          {renderStepContent(activeStep)}
          
          <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 3 }}>
            {activeStep !== 0 && (
              <Button
                onClick={handleBack}
                sx={{ mr: 1 }}
              >
                
              </Button>
            )}
            {activeStep === steps.length - 1 ? (
              <Button
                variant="contained"
                color="primary"
                type="submit"
              >
                
              </Button>
            ) : (
              <Button
                variant="contained"
                onClick={handleNext}
              >
                
              </Button>
            )}
          </Box>
        </form>
      </Paper>
    </Container>
  );
}

export default DetailedFinancialForm;
